.button {
  color: #000;
  border: 1px, black;
  cursor: pointer;
  text-align: center;
  background-color: #e7e7e7;
  border-radius: 10px;
  margin-left: 50px;
  padding: 15px 32px;
  font-size: 16px;
  text-decoration: none;
  transition: background-color .25s cubic-bezier(.4, 0, .2, 1) .1s;
  display: inline-block;
  box-shadow: 0 4px 4px rgba(0, 0, 0, .15);
}

.button:hover, .button:focus {
  color: wheat;
  background-color: #000;
}

